.testimonials {
  padding: 3rem 2rem;
  background-color: #f9f9f9;
  color: #222;

  .section-description {
    text-align: center;
    font-size: 1.1rem;
    color: #555;
    margin-bottom: 2rem;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }

  .section-title {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 2rem;
    font-weight: 700;
    font-family: "Poppins", sans-serif;

    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.6s ease, transform 0.6s ease;

    &[data-aos].aos-animate {
      opacity: 1;
      transform: translateY(0);
    }
  }


  .testimonials-container {
    position: relative;
    overflow: hidden;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.6s ease 0.2s, transform 0.6s ease 0.2s;

    &[data-aos].aos-animate {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .slider-inner {
    display: flex;
    transition: transform 0.6s ease-in-out;
    width: 100%;
  }

  .testimonial-card {
    background: white;
    border-radius: 12px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 320px;
    padding: 0;
    overflow: hidden;
    transition: box-shadow 0.4s ease;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-shrink: 0;

    opacity: 0;
    transform: translateY(30px);

    &[data-aos].aos-animate {
      opacity: 1;
      transform: translateY(0);
    }

    .card-inner {
      padding: 2rem 1.5rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      transition: transform 0.4s ease;

      .testimonial-image {
        width: 110px;
        height: 110px;
        margin-bottom: 1.2rem;
        overflow: hidden;
        border-radius: 50%;
        border: 3px solid #ddd;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .testimonial-text {
        font-style: italic;
        color: #555;
        margin-bottom: 1.5rem;
        font-size: 1rem;
        line-height: 1.5;
        min-height: 70px;
      }

      .testimonial-name {
        font-weight: 700;
        font-size: 1.2rem;
        margin-bottom: 0.3rem;
        color: #222;
      }

      .testimonial-role {
        font-size: 0.9rem;
        color: #777;
        margin-bottom: 1rem;
      }

      .linkedin-link {
        font-weight: 600;
        color: #0a66c2;
        text-decoration: none;
        border: 1px solid #0a66c2;
        padding: 0.4rem 1rem;
        border-radius: 5px;
        transition: background-color 0.3s ease, color 0.3s ease;

        &:hover {
          background-color: #0a66c2;
          color: white;
        }
      }
    }

    &:hover {
      box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);

      .card-inner {
        transform: translateY(-12px);
      }
    }
  }

  .arrow {
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    font-size: 2.5rem;
    color: #333;
    background: transparent;
    border: none;
    cursor: pointer;
    z-index: 2;
    user-select: none;
    transition: color 0.3s ease;

    &.prev {
      left: 10px;
    }

    &.next {
      right: 10px;
    }

    &:hover {
      color: #000;
    }
  }

  .dots {
    display: flex;
    justify-content: center;
    gap: 0.7rem;
    margin-top: 1.5rem;

    .dot {
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background: #ccc;
      cursor: pointer;
      transition: background 0.3s ease;

      &.active {
        background: #333;
      }
    }
  }

@media screen and (max-width: 768px) {
  .testimonials {
    padding: 2rem 1rem;

    .testimonials-container {
      overflow-x: hidden; /* Prevent horizontal scroll */
      max-width: 100vw; /* Limit to viewport width */
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      padding-bottom: 1rem;
    }

    .slider-inner {
      display: flex !important;
      width: 100% !important;
      transition: none !important;
      transform: none !important;
      justify-content: center;
      gap: 1rem;
      flex-wrap: nowrap;
      overflow: visible;
    }

    .testimonial-card {
      flex: 0 0 auto;
      width: 90vw;       /* 90% of viewport width */
      max-width: 320px;  /* Max width */
      margin: 0 auto;
      cursor: grab;
      scroll-snap-align: center;
      background: #fff;
      padding: 1.5rem;
      border-radius: 12px;
      box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);

      /* Make sure text wraps inside */
      overflow-wrap: break-word;
      word-wrap: break-word;
      word-break: break-word;
    }

    .testimonial-image {
      width: 80px;
      height: 80px;
      margin-bottom: 1rem;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
      }
    }

    .testimonial-text {
      font-size: 1rem;
      line-height: 1.5;
      margin-bottom: 1rem;
      max-width: 100%;      /* make sure it doesn't exceed container */
      white-space: normal;  /* wrap text normally */
      overflow-wrap: break-word;
    }

    .testimonial-name {
      font-weight: 700;
      font-size: 1.2rem;
      color: #222;
      margin-bottom: 0.3rem;
    }

    .testimonial-role {
      font-size: 0.9rem;
      color: #777;
      margin-bottom: 1rem;
    }

    .arrow,
    .dots {
      display: none;  /* Hide arrows and dots on mobile */
    }
  }
}

}
