.contact {
  padding: 4rem 2rem;
  background-color: #f6f7fa;
  color: #0e1117;

  .container {
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
    justify-content: space-between;
  }

  .contact-info {
    flex: 1;
    min-width: 280px;

    h2 {
      font-size: 2rem;
      margin-bottom: 1rem;
    }

    p {
      font-size: 1.1rem;
      line-height: 1.6;
      margin-bottom: 2rem;
    }

    .info-item {
      display: flex;
      align-items: center;
      gap: 1rem;
      margin-bottom: 1.5rem;

      .icon {
        font-size: 2rem;
        color: #fff;
        background-color: #1c1f26;
        padding: 0.7rem;
        border-radius: 0.5rem;
      }

      h4 {
        margin: 0;
        font-size: 1.1rem;
      }

      p {
        margin: 0;
        font-size: 1rem;
        opacity: 0.85;
      }
    }
  }

  .contact-form {
    flex: 1;
    min-width: 300px;
    background: rgba(255, 255, 255, 0.05);
    padding: 2rem;
    border-radius: 1rem;
    backdrop-filter: blur(8px);

    .form-row {
      display: flex;
      gap: 1rem;
      flex-wrap: wrap;
    }

    .form-group {
      flex: 1;
      display: flex;
      flex-direction: column;
      margin-bottom: 1.5rem;

      label {
        margin-bottom: 0.5rem;
        font-weight: 600;
      }

      input,
      textarea {
        padding: 0.8rem 1rem;
        border: none;
        border-radius: 0.5rem;
        background-color: #2c2f36;
        color: #fff;
        font-size: 1rem;

        &::placeholder {
          color: #ddd5cb;
        }

        &:focus {
          outline: 2px solid #00bcd4;
        }
      }
    }

    .form-group.full {
      width: 100%;
    }

    button {
      margin-top: 1rem;
      width: 100%;
      padding: 1rem;
      background-color: #182b44;
      border: none;
      border-radius: 0.5rem;
      font-weight: bold;
      font-size: 1rem;
      color: #fff;
      cursor: pointer;
      transition: background 0.3s;

      &:hover {
        background-color: #69ab5d;
      }
    }
    .form-error {
      color: #ff5f5f;
      margin-top: 1rem;
      font-size: 0.95rem;
      font-weight: bold;
    }
    .form-success {
      color: #4caf58;
      margin-top: 1rem;
      font-size: 0.95rem;
      font-weight: bold;
    }
  }
}
