.footer {
  background-color: #0f214d;
  color: #ddd;
  padding: 2rem 1rem;
  text-align: center;

  .container {
    max-width: 1200px;
    margin: auto;
    padding: 0 1rem;
  }

  .logo {
    font-size: 1.8rem;
    font-weight: bold;
    color: #edeced;
    margin-bottom: 1.2rem;

    @media (max-width: 600px) {
      font-size: 1.5rem;
    }
  }

  .nav-links {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
    flex-wrap: wrap; // wraps items on small screens
    gap: 1rem;
    margin-bottom: 1.5rem;

    li {
      a {
        color: #bbb;
        font-weight: 500;
        text-decoration: none;
        transition: color 0.3s;
        font-size: 1rem;

        &:hover {
          color: #cae4f3;
        }

        @media (max-width: 600px) {
          font-size: 0.95rem;
        }
      }
    }
  }

  .socials {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1.5rem;

    a {
      font-size: 1.5rem;
      color: #bbb;
      transition: color 0.3s;

      &:hover {
        color: #cae4f3;
      }

      @media (max-width: 600px) {
        font-size: 1.3rem;
      }
    }
  }

  .copyright {
    font-size: 0.9rem;
    color: #888;

    @media (max-width: 600px) {
      font-size: 0.8rem;
    }
  }

  .copyright2 {
    font-size: 0.9rem;
    color: #fbff02;

    @media (max-width: 600px) {
      font-size: 0.8rem;
    }
  }
  
}
