.projects {
  padding: 2rem 1rem;
  max-width: 1200px;
  margin: 0 auto;

  .section-title {
    font-size: 2.5rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 0.25rem;
    color: #222;
  }

  .section-description {
    text-align: center;
    font-size: 1.1rem;
    color: #555;
    margin-bottom: 2rem;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }

  .main-projects,
  .sub-projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
  }

  .project-card {
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: default;

    &:hover {
      transform: translateY(-8px);
      box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
    }

    .image-container {
      position: relative;
      overflow: hidden;

      img {
        display: block;
        width: 100%;
        height: 290px;
        object-fit: cover;
        transition: transform 0.3s ease;
        border-radius: 12px 12px 0 0;
      }

      &:hover img {
        transform: scale(1.05);
      }

      .hover-overlay {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.55);
        color: #fff;
        opacity: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1rem;
        padding: 0;
        transition: opacity 0.3s ease;
        z-index: 2;

        a {
          background: rgba(255, 255, 255, 0.15);
          padding: 0.6rem 1.2rem;
          border-radius: 6px;
          color: #fff;
          font-weight: 600;
          font-size: 1rem;
          display: flex;
          align-items: center;
          gap: 0.5rem;
          text-decoration: none;
          transition: background 0.3s ease;

          &:hover {
            background: #3f51b5;
          }
        }
      }

      &:hover .hover-overlay {
        opacity: 1;
      }
    }

    .info {
      padding: 1rem 1.25rem;

      h3 {
        margin: 0.5rem 0 0.25rem 0;
        font-size: 1.6rem;
        color: #222;
      }

      p.description {
        font-size: 1rem;
        color: #555;
        margin-bottom: 0.75rem;
        max-width: 90%;
      }

      .icons {
        display: flex;
        gap: 0.75rem;
        font-size: 1.4rem;
        color: #3f51b5;
        margin-bottom: 0.75rem;
      }
    }
  }

  .main-projects {
    .project-card.main {
      flex: 1 1 45%;
      max-width: 45%;
      min-width: 300px;
    }
  }

  .sub-projects {
    margin-top: 3rem;

    .project-card.sub {
      flex: 1 1 30%;
      max-width: 30%;
      min-width: 220px;

      .icons {
        display: none;
      }

      .image-container {
        img {
          max-height: 190px;
          object-fit: cover;
          border-radius: 12px 12px 0 0;
        }
      }
    }
  }

  @media (max-width: 900px) {
    .main-projects,
    .sub-projects {
      flex-direction: column;
      align-items: center;
    }

    .main-projects .project-card.main,
    .sub-projects .project-card.sub {
      max-width: 90%;
      flex-basis: 100%;
    }
  }
}
