.about {
  padding: 4rem 2rem; // slightly reduced vertical padding
  background-color: #f9f9f9;
  color: #111;

  &__container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
    gap: 4rem;
    transform: translateY(-20px); // shift the whole container upward

    @media (max-width: 1024px) {
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 3rem;
      transform: none; // don’t shift on smaller screens
    }
  }

  &__content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: -10px; // slight upward bump for text content

    .about__title {
      font-size: 2.5rem;
      margin-bottom: 1rem;
    }

    .about__subtitle {
      font-size: 1.25rem;
      margin-bottom: 1rem;
      color: #666;
    }

    .about__description {
      font-size: 1rem;
      margin-bottom: 2rem;
      line-height: 1.6;
    }

    .about__buttons {
      display: flex;
      flex-wrap: wrap;
      gap: 1rem;

      @media (max-width: 768px) {
        justify-content: center;
        flex-direction: column;
        align-items: center;
      }

      .btn {
        padding: 0.75rem 1.5rem;
        background-color: #111;
        color: #fff;
        border: none;
        border-radius: 4px;
        text-decoration: none;
        font-weight: 500;
        transition: background 0.3s;

        &:hover {
          background-color: #333;
        }

        &--secondary {
          background-color: transparent;
          color: #111;
          border: 1px solid #111;

          &:hover {
            background-color: #111;
            color: #fff;
          }
        }
      }
    }
  }

  &__image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start; // aligns image to top
    margin-top: -10px; // slight upward bump for the image

    img {
      width: 100%;
      max-width: 400px;
      border-radius: 12px;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    }
  }
}
